import React, {
  createContext,
  // useState,
  useContext,
} from "react";
import {
  SEOContextProps,
  SEOMetadata,
} from "../types";

// Create context with default values
const SEOContext = createContext<SEOContextProps>({
  defaultSEO: { title: "" } as SEOMetadata,
  currentSEO: null as SEOMetadata | null,
  updateCurrentSEO: () => {},
  clearCurrentSEO: () => {},
});

/**
 * Hook to use the SEO context
 * @returns SEO context values and methods
 */
export const useSEO = (): SEOContextProps => {
  const context = useContext(SEOContext);

  if (!context) {
    throw new Error("useSEO must be used within an SEOProvider");
  }

  return context;
};

export default SEOContext;
