import React, { useState } from "react";
import SEOContext from "../context/SEOContext";
import { SEOProviderProps, SEOMetadata } from "../types";

/**
 * SEO Provider component to provide SEO context to the application
 */
const SEOProvider: React.FC<SEOProviderProps> = ({ defaultSEO, children }) => {
  const [currentSEO, setCurrentSEO] = useState<SEOMetadata | null>(null);

  /**
   * Update current page's SEO
   * @param seo - SEO metadata
   */
  const updateCurrentSEO = (seo: SEOMetadata) => {
    setCurrentSEO(seo);
  };

  /**
   * Clear current page's SEO
   */
  const clearCurrentSEO = () => {
    setCurrentSEO(null);
  };

  return (
    <SEOContext.Provider
      value={{
        defaultSEO,
        currentSEO,
        updateCurrentSEO,
        clearCurrentSEO,
      }}
    >
      {children}
    </SEOContext.Provider>
  );
};

export { SEOProvider };
export default SEOProvider;
