import { SEOMetadata, OpenGraphImage, MetaTag } from "../types";

/**
 * Deep merge two objects
 * @param target - Target object
 * @param source - Source object
 * @returns Merged object
 */
type DeepPartial<T> = {
  [P in keyof T]?: T[P] extends object ? DeepPartial<T[P]> : T[P];
};

// Helper to check if something is a plain object (not array, null, etc.)
const isPlainObject = (obj: any): obj is Record<string, any> => {
  return obj && typeof obj === "object" && !Array.isArray(obj);
};

export const deepMerge = <T extends Record<string, any>>(
  target: T,
  source: DeepPartial<T>
): T => {
  // Create a new object to avoid mutations
  const output: any = { ...target } as T;

  if (!source) {
    return output;
  }

  Object.keys(source).forEach((key) => {
    // Safe way to index with string keys
    if (key in source) {
      const targetValue = target[key];
      const sourceValue = source[key as keyof DeepPartial<T>];

      // If both values are objects, recursively merge them
      if (isPlainObject(targetValue) && isPlainObject(sourceValue)) {
        // This cast is safe because we've verified both are objects
        output[key] = deepMerge(
          targetValue,
          sourceValue as DeepPartial<typeof targetValue>
        );
      }
      // Otherwise use source value if defined, or keep target value
      else if (sourceValue !== undefined) {
        // Using type assertion to handle the assignment
        output[key] = sourceValue as any;
      }
    }
  });

  return output;
};

/**
 * Format OpenGraph image to meta tags
 * @param image - OpenGraph image or images
 * @returns Array of meta tags
 */
export const formatOpenGraphImage = (
  image?: OpenGraphImage | OpenGraphImage[]
): MetaTag[] => {
  if (!image) {
    return [];
  }

  const images = Array.isArray(image) ? image : [image];

  return images.reduce<MetaTag[]>((acc, img, index) => {
    const prefix = index === 0 ? "og:image" : `og:image:0${index + 1}`;

    const tags: MetaTag[] = [{ property: prefix, content: img.url }];

    if (img.alt) {
      tags.push({ property: `${prefix}:alt`, content: img.alt });
    }

    if (img.width) {
      tags.push({ property: `${prefix}:width`, content: String(img.width) });
    }

    if (img.height) {
      tags.push({ property: `${prefix}:height`, content: String(img.height) });
    }

    if (img.type) {
      tags.push({ property: `${prefix}:type`, content: img.type });
    }

    return [...acc, ...tags];
  }, []);
};

/**
 * Generate meta tags for SEO metadata
 * @param seo - SEO metadata
 * @returns Array of meta tags
 */
export const generateMetaTags = (seo: SEOMetadata): MetaTag[] => {
  const tags: MetaTag[] = [];

  // Basic meta tags
  if (seo.description) {
    tags.push({ name: "description", content: seo.description });
  }

  // Keywords meta tags
  if (seo.keywords) {
    tags.push({ name: "keywords", content: seo.keywords });
  }

  // Robots meta tags
  const robotsContent: string[] = [];
  if (seo.noIndex) robotsContent.push("noindex");
  if (seo.noFollow) robotsContent.push("nofollow");

  if (robotsContent.length > 0) {
    tags.push({ name: "robots", content: robotsContent.join(", ") });
  }

  // OpenGraph tags
  if (seo.openGraph) {
    const { openGraph } = seo;

    if (openGraph.title || seo.title) {
      tags.push({
        property: "og:title",
        content: openGraph.title || seo.title,
      });
    }

    if (openGraph.description || seo.description) {
      tags.push({
        property: "og:description",
        content: openGraph.description || seo.description || "",
      });
    }

    if (openGraph.url) {
      tags.push({ property: "og:url", content: openGraph.url });
    } else if (seo.canonical) {
      tags.push({ property: "og:url", content: seo.canonical });
    }

    if (openGraph.type) {
      tags.push({ property: "og:type", content: openGraph.type });
    }

    if (openGraph.siteName) {
      tags.push({ property: "og:site_name", content: openGraph.siteName });
    }

    if (openGraph.locale) {
      tags.push({ property: "og:locale", content: openGraph.locale });
    }

    // Handle OG images
    if (openGraph.image) {
      const imageTags = formatOpenGraphImage(openGraph.image);
      tags.push(...imageTags);
    }
  }

  // Twitter tags
  if (seo.twitter) {
    const { twitter } = seo;

    if (twitter.cardType) {
      tags.push({ name: "twitter:card", content: twitter.cardType });
    }

    if (twitter.handle) {
      tags.push({ name: "twitter:creator", content: twitter.handle });
    }

    if (twitter.site) {
      tags.push({ name: "twitter:site", content: twitter.site });
    }

    if (twitter.title || seo.title) {
      tags.push({
        name: "twitter:title",
        content: twitter.title || seo.title,
      });
    }

    if (twitter.description || seo.description) {
      tags.push({
        name: "twitter:description",
        content: twitter.description || seo.description || "",
      });
    }

    if (twitter.image) {
      tags.push({ name: "twitter:image", content: twitter.image });

      if (twitter.imageAlt) {
        tags.push({ name: "twitter:image:alt", content: twitter.imageAlt });
      }
    }
  }

  // Add any additional meta tags
  if (seo.additionalMetaTags) {
    tags.push(...seo.additionalMetaTags);
  }

  return tags;
};

/**
 * Format JSON-LD script content
 * @param jsonLd - JSON-LD data
 * @returns Formatted JSON-LD string
 */
export const formatJsonLd = (jsonLd?: string | Record<string, any>): string => {
  if (!jsonLd) {
    return "";
  }

  if (typeof jsonLd === "string") {
    return jsonLd;
  }

  return JSON.stringify(jsonLd);
};
